___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: #d7d7d7;
    color: #3A3132;
    font-family: 'Jost', sans-serif;
}

.skip-link {
    position: absolute;
    top: -40px;
    left: 0;
    background-color: #bf1722;
    color: white;
    padding: 8px;
    z-index: 99999;
}

.skip-link:focus {
    top: 0;
}

header {
    background-color: #EEEEEE;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    padding: 15px 0;
    position: fixed;
    z-index: 999;
}

.logo h1 {
    font-family: 'Pacifico', sans-serif;
    font-weight: 600;
    letter-spacing: 2px;
    font-size: 26px;
}

nav {
    width: 100%;
}

nav ul {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    list-style: none;
    align-items: center;
    padding: 10px 0;
    transition: all 1s;
}

nav ul li {
    min-width: 44px;
    min-height: 44px;
}

nav ul.hidden {
    display: none;

}


nav ul li a {
    text-decoration: none;
    color: #3A3132;
    font-size: 24px;
    padding: 5px;
    font-weight: 500;
    transition: all ease-in-out 0.5s;
}

nav ul li a:hover {
    font-weight: 700;
    text-decoration: underline;
}

#hamburger {
    min-width: 44px;
    min-height: 44px;
    font-size: 28px;
    font-weight: 600;
    cursor: pointer;
    color: #333;
    text-decoration: none;
    transition: 0.3s;
}

#hamburger:hover {
    font-weight: 900;
}

#drawer {
    width: 100%;
    height: 150px;
    position: absolute;
    transform: translate(0, -200px);
    transition: transform 0.3s ease-in-out;
    background-color: #EEEEEE;
}

#drawer.open {
    transform: translate(0, 35px);
}

.hero {
    height: 100vh;
    width: 100%;
    background-position: center;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-repeat: no-repeat;
    background-size: cover;
    border-image: fill 0 linear-gradient(#0001, #000);
    box-sizing: border-box;
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
}

.hero .inner {
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    padding-bottom: 100px;
}

.hero .inner h2 {
    font-size: 30px;
    text-align: center;
    color: #dddddd;
    padding: 0px 10px 30px;
}

.hero .inner p {
    font-family: 'Rubik';
    font-size: 20px;
    text-align: center;
    color: #ffffff;
    word-spacing: 2px;
    padding: 0px 13px 30px;
}

.explore-now {
    padding: 15px 30px;
    border: 2px solid #2c2c2c;
    background-color: #1a1a1a;
    color: #ffffff;
    font-size: 1.2rem;
    cursor: pointer;
    border-radius: 30px;
    transition: all 0.4s ease;
    outline: none;
    position: relative;
    overflow: hidden;
    font-weight: bold;
    margin-top: 70px;
}

.explore-now::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle,
            rgba(255, 255, 255, 0.25) 0%,
            rgba(255, 255, 255, 0) 70%);
    transform: scale(0);
    transition: transform 0.5s ease;
}

.explore-now:hover::after {
    transform: scale(4);
}

.explore-now:hover {
    border-color: #666666;
    background: #292929;
}

.explore-now:focus {
    border-color: #666666;
    background: #292929;
}

.explore {
    margin: 40px 0 20px;
}

.explore h3 {
    text-align: center;
    padding: 20px;
    font-size: 30px;
    font-family: 'Rubik';
}


.card {
    margin: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.card img {
    width: 100%;
    height: 200px;
    object-fit: cover;
    object-position: center;
    border-radius: 5px;
}

.card-content {
    padding: 16px;

}

.card-content a {
    text-decoration: none;
    min-width: 44px;
    min-height: 44px;
    display: flex;
    justify-content: space-around;
    font-family: 'Rubik';
    padding: 2px;
    color: #666666;
    transition: all ease-in-out 0.5s;
}

.card-content a:hover {
    font-weight: bold;
    text-decoration: underline;
}

.card h2 {
    margin: 0 0 8px;
}

.card p {
    margin: 0 0 4px;
}

#restaurants {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1rem;
}

.container {
    transition: all 0.5s ease;
    margin: 5px;
    border-radius: 10px;
}

.container:hover {
    transform: scale(1) translateY(-10px);
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.like {
    width: 55px;
    height: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #db0000;

    position: fixed;
    bottom: 16px;
    right: 16px;
    border-radius: 50%;
    border: 0;

    font-size: 18px;
    color: white;
    cursor: pointer;
}

.footer {
    background-color: #333;
    color: #fff;
    padding: 50px 0;
}

.container-footer {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 1.5rem;
}

.footer-column {
    margin-bottom: 20px;
    padding-left: 20px;
    display: none;
}

.footer-column h3 {
    margin-top: 0;
    padding: 5px 0;
    font-size: 24px;
}

.footer-column ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.footer-column li {
    margin-bottom: 5px;
    min-width: 44px;
    min-height: 44px;

}

.footer-column a {
    color: #fff;
    text-decoration: none;
    font-size: 20px;
    padding: 9px;
}

.footer-column a:hover {
    text-decoration: underline;
}


.copyright {
    text-align: center;
    font-size: 20px;
}


@media screen and (max-width: 600px) {
    .hero {
        width: 100%;
        height: 100vh;
        background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-image: fill 0 linear-gradient(#0001, #000);
        box-sizing: border-box;
    }
}


/*breakpoint Tablet*/

@media screen and (min-width: 768px) {
    #drawer {
        position: relative;
        transform: translate(0, 0);
        background-color: transparent;
        display: flex;
        height: 76px;
        justify-content: space-around;
    }

    #restaurants {
        grid-template-columns: repeat(2, 1fr);
        gap: 0;
    }

    .card img {
        height: 300px;
    }

    #hamburger {
        display: none;
    }

    nav {
        width: 70%;

    }

    nav ul {
        display: flex;
        flex-direction: row;
        width: 100%;
        align-items: center;
    }

    header {
        flex-wrap: nowrap;
    }

    .logo h1 {
        font-size: 26px;
        padding: 2px;
    }

    nav ul li {
        min-width: 44px;
        min-height: 44px;
    }

    nav ul li a {
        font-size: 23px;
        padding: 6px;
    }

}



/*breakPoint Laptop*/

@media screen and (min-width: 1024px) {
    #restaurants {
        grid-template-columns: 1fr 1fr 1fr;
        gap: 0;
    }

    .card img {
        height: 300px;
    }


    .footer-column {
        display: grid;
    }

    .logo h1 {
        font-size: 28px;
    }
}

/*breakpoint desktop/ > 1200px */

@media screen and (min-width: 1200px) {
    #restaurants {
        grid-template-columns: repeat(4, 1fr);
    }

    .hero {
        min-width: 1000px;
    }

    #restaurants>.container:nth-child(20n+20) {
        grid-column: -1/4;
    }


    .logo h1 {
        padding-left: 20px;

    }

    .order {
        margin-right: 20px;
    }

    .logo h1 {
        font-size: 30px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;IACI,UAAU;IACV,SAAS;IACT,sBAAsB;AAC1B;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,+BAA+B;AACnC;;AAEA;IACI,kBAAkB;IAClB,UAAU;IACV,OAAO;IACP,yBAAyB;IACzB,YAAY;IACZ,YAAY;IACZ,cAAc;AAClB;;AAEA;IACI,MAAM;AACV;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,eAAe;IACf,WAAW;IACX,6BAA6B;IAC7B,mBAAmB;IACnB,eAAe;IACf,eAAe;IACf,YAAY;AAChB;;AAEA;IACI,mCAAmC;IACnC,gBAAgB;IAChB,mBAAmB;IACnB,eAAe;AACnB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,6BAA6B;IAC7B,gBAAgB;IAChB,mBAAmB;IACnB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;;AAEjB;;;AAGA;IACI,qBAAqB;IACrB,cAAc;IACd,eAAe;IACf,YAAY;IACZ,gBAAgB;IAChB,gCAAgC;AACpC;;AAEA;IACI,gBAAgB;IAChB,0BAA0B;AAC9B;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,eAAe;IACf,gBAAgB;IAChB,eAAe;IACf,WAAW;IACX,qBAAqB;IACrB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,aAAa;IACb,kBAAkB;IAClB,+BAA+B;IAC/B,sCAAsC;IACtC,yBAAyB;AAC7B;;AAEA;IACI,6BAA6B;AACjC;;AAEA;IACI,aAAa;IACb,WAAW;IACX,2BAA2B;IAC3B,yDAAqE;IACrE,4BAA4B;IAC5B,sBAAsB;IACtB,iDAAiD;IACjD,sBAAsB;IACtB,aAAa;IACb,6BAA6B;IAC7B,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,6BAA6B;IAC7B,sBAAsB;IACtB,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,sBAAsB;AAC1B;;AAEA;IACI,oBAAoB;IACpB,eAAe;IACf,kBAAkB;IAClB,cAAc;IACd,iBAAiB;IACjB,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,yBAAyB;IACzB,cAAc;IACd,iBAAiB;IACjB,eAAe;IACf,mBAAmB;IACnB,yBAAyB;IACzB,aAAa;IACb,kBAAkB;IAClB,gBAAgB;IAChB,iBAAiB;IACjB,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ;;uCAEmC;IACnC,mBAAmB;IACnB,+BAA+B;AACnC;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;IACrB,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,eAAe;IACf,oBAAoB;AACxB;;;AAGA;IACI,YAAY;IACZ,sBAAsB;IACtB,kBAAkB;IAClB,wCAAwC;AAC5C;;AAEA;IACI,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,uBAAuB;IACvB,kBAAkB;AACtB;;AAEA;IACI,aAAa;;AAEjB;;AAEA;IACI,qBAAqB;IACrB,eAAe