// Module
___CSS_LOADER_EXPORT___.push([module.id, `.detail-content {
    padding: 100px 20px 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.resto-name {
    font-size: 24px;
    margin-bottom: 10px;
    text-align: center;
}

.resto-image {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 20px;
}

.resto-info h2 {
    font-size: 20px;
    margin-top: 20px;
    border-bottom: 2px solid #333;
    padding-bottom: 5px;
}

.resto-info h4 {
    font-size: 16px;
    margin-top: 10px;
}

.resto-info p {
    margin: 5px 0;
}

.resto-description {
    margin-top: 30px;
    text-align: justify;
}

.resto-description h3 {
    margin-bottom: 5px;
}

.restaurant-menu h3 {
    font-size: 20px;
    margin-bottom: 10px;
}

.restaurant-menu {
    display: flex;
    justify-content: space-around;
    padding-top: 30px;
}

.restaurant-menu ul {
    list-style-type: square;
    padding-left: 20px;
}

.restaurant-menu li {
    margin-bottom: 5px;
}


.customer-review {
    background-color: transparent;
    padding: 20px 0px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.-customer-review-item {
    display: grid;
    grid-template-columns:  1fr 1fr;
    background-color: #f5f5f5;
    padding: 10px 30px 10px 20px;
    gap: 10px;
}

.customer-review-masukan {
    max-width: 162px;
    text-overflow: ellipsis;
    overflow: hidden;
}

.like {
    background-color: #ff7f50;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.like:hover {
    background-color: #ff6347;
}
